import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { of } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';

import { LoggerService } from './logger.service';
import { AuthService } from './auth.service';
import { environment } from '../../../../environments/environment';
import { Banner } from '../classes/banner';

@Injectable({
  providedIn: 'root'
})
export class BannerService {

  private apiURL: string = `${environment.apiURL}/banners`;
  options: HttpHeaders = new HttpHeaders();
  session: any = {};

  constructor(private http: HttpClient, private loggerService: LoggerService, private authService: AuthService) {
    this.options = new HttpHeaders( {'Content-Type': 'application/json', 'x-access-token': localStorage.getItem('tokenUserLogged')} );
  }

  private logError (error, operation) {
    let message = '';
    let reload = false;
    if (error.status === 404) {
      message = 'Falha ao se comunicar com a API: Rota no encontrada (ERRO 404)';
    } else if (error.status === 401) {
      message = 'Falha ao se comunicar com a API: Token invlido ou expirado';
      reload = this.authService.logoff();
    } else if (error.status === 400) {
      message = `Os dados enviados so invlidos! \nCausa do erro: ${error.error.details[0].message}`;
    } else if (error.status >= 500 && error.status <= 505) {
      message = `Houve algum erro no servidor! Entre em contato com o administrador!`;
    } else {
      message = error.Message;
    }

    this.loggerService.add(`BannerService failed on operation: ${operation}\nMessage: ${message}`);
    if (reload) {
      location.reload();
    }
  }

  private log (mensagem: string) {
    this.loggerService.add('BannerService: ' + mensagem);
  }

  getBanner (keyBanner: string): Observable<any> {
    return this.http.get(`${this.apiURL}/${keyBanner}`, { headers: this.options }).pipe(
      tap(_ => this.log(`fetched banner with id=${keyBanner}`)),
      catchError(this.handleError<Banner>('getBanner'))
    );
  }

  getBanners (): Observable<any[]> {
    return this.http.get<any[]>(`${this.apiURL}`, { headers: this.options }).pipe(
      tap(banners => this.log(`fetched banners`)),
      catchError(this.handleError('getBanners', []))
    );
  }

  addBanner (banner: Banner): Observable<any> {
    return this.http.post<Banner>(`${this.apiURL}`, banner, { headers: this.options }).pipe(
      tap((banner: Banner) => this.log(`added banner w/ key=${banner._id}`)),
      catchError(this.handleError<Banner>('addBanner'))
    );
  }

  updateBanner (keyBanner: string, banner: any): Observable<any> {
    return this.http.put(`${this.apiURL}/${keyBanner}`, banner, { headers: this.options }).pipe(
      tap((response: any) => this.log(`approval banner w/ id=${response._key}`)),
      catchError(this.handleError<Banner>('updateBanner'))
    )
  }

  removeBanner (keyBanner: string): Observable<any> {
    return this.http.delete(`${this.apiURL}/${keyBanner}`, { headers: this.options }).pipe(
      tap(_ => this.log(`banner deleted with id=${keyBanner}.`)),
      catchError(this.handleError<Banner>('removeBanner'))
    );
  }

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      this.logError(error, operation);
      return of(result as T);
    };
  }

}