import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { of } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';

import { LoggerService } from './logger.service';
import { AuthService } from './auth.service';
import { environment } from '../../../../environments/environment';
import { Album } from '../classes/album';

@Injectable({
  providedIn: 'root'
})
export class AlbumService {

  private apiURL: string = `${environment.apiURL}/albuns`;
  options: HttpHeaders = new HttpHeaders();
  session: any = {};

  constructor(private http: HttpClient, private loggerService: LoggerService, private authService: AuthService) {
    this.options = new HttpHeaders( {'Content-Type': 'application/json', 'x-access-token': localStorage.getItem('tokenUserLogged')} );
  }

  private logError (error, operation) {
    let message = '';
    let reload = false;
    if (error.status === 404) {
      message = 'Falha ao se comunicar com a API: Rota no encontrada (ERRO 404)';
    } else if (error.status === 401) {
      message = 'Falha ao se comunicar com a API: Token invlido ou expirado';
      reload = this.authService.logoff();
    } else if (error.status === 400) {
      message = `Os dados enviados so invlidos! \nCausa do erro: ${error.error.details[0].message}`;
    } else if (error.status >= 500 && error.status <= 505) {
      message = `Houve algum erro no servidor! Entre em contato com o administrador!`;
    } else {
      message = error.Message;
    }

    this.loggerService.add(`AlbumService failed on operation: ${operation}\nMessage: ${message}`);
    if (reload) {
      location.reload();
    }
  }

  private log (mensagem: string) {
    this.loggerService.add('AlbumService: ' + mensagem);
  }

  getAlbum (keyAlbum: string): Observable<any> {
    return this.http.get(`${this.apiURL}/${keyAlbum}`, { headers: this.options }).pipe(
      tap(_ => this.log(`fetched album with id=${keyAlbum}`)),
      catchError(this.handleError<Album>('getAlbum'))
    );
  }

  getAlbuns (): Observable<any[]> {
    return this.http.get<any[]>(`${this.apiURL}`, { headers: this.options }).pipe(
      tap(tests => this.log(`fetched albuns`)),
      catchError(this.handleError('getAlbuns', []))
    );
  }

  addAlbum (album: Album): Observable<any> {
    return this.http.post<Album>(`${this.apiURL}`, album, { headers: this.options }).pipe(
      tap((album: Album) => this.log(`added album w/ key=${album._key}`)),
      catchError(this.handleError<Album>('addAlbum'))
    );
  }

  updateAlbum (keyAlbum: string, album: any): Observable<any> {
    return this.http.put(`${this.apiURL}/${keyAlbum}`, album, { headers: this.options }).pipe(
      tap((response: any) => this.log(`album updated w/ id=${response._key}`)),
      catchError(this.handleError<Album>('updateAlbum'))
    )
  }

  removeAlbum (keyAlbum: string): Observable<any> {
    return this.http.delete(`${this.apiURL}/${keyAlbum}`, { headers: this.options }).pipe(
      tap(_ => this.log(`album deleted with id=${keyAlbum}.`)),
      catchError(this.handleError<Album>('removeAlbum'))
    );
  }

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      this.logError(error, operation);
      return of(result as T);
    };
  }

}