import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { of } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';

import { LoggerService } from './logger.service';
import { AuthService } from './auth.service';
import { environment } from '../../../../environments/environment';
import { Download } from '../classes/download';

@Injectable({
  providedIn: 'root'
})
export class DownloadService {

  private apiURL: string = `${environment.apiURL}/downloads`;
  options: HttpHeaders = new HttpHeaders();
  session: any = {};

  constructor(private http: HttpClient, private loggerService: LoggerService, private authService: AuthService) {
    this.options = new HttpHeaders( {'Content-Type': 'application/json', 'x-access-token': localStorage.getItem('tokenUserLogged')} );
  }

  private logError (error, operation) {
    let message = '';
    let reload = false;
    if (error.status === 404) {
      message = 'Falha ao se comunicar com a API: Rota no encontrada (ERRO 404)';
    } else if (error.status === 401) {
      message = 'Falha ao se comunicar com a API: Token invlido ou expirado';
      reload = this.authService.logoff();
    } else if (error.status === 400) {
      message = `Os dados enviados so invlidos! \nCausa do erro: ${error.error.details[0].message}`;
    } else if (error.status >= 500 && error.status <= 505) {
      message = `Houve algum erro no servidor! Entre em contato com o administrador!`;
    } else {
      message = error.Message;
    }

    this.loggerService.add(`DownloadService failed on operation: ${operation}\nMessage: ${message}`);
    if (reload) {
      location.reload();
    }
  }

  private log (mensagem: string) {
    this.loggerService.add('DownloadService: ' + mensagem);
  }

  getDownload (keyDownload: string): Observable<any> {
    return this.http.get(`${this.apiURL}/${keyDownload}`, { headers: this.options }).pipe(
      tap(_ => this.log(`fetched download with id=${keyDownload}`)),
      catchError(this.handleError<Download>('getDownload'))
    );
  }

  getDownloads (): Observable<any[]> {
    return this.http.get<any[]>(`${this.apiURL}`, { headers: this.options }).pipe(
      tap(downloads => this.log(`fetched downloads`)),
      catchError(this.handleError('getDownloads', []))
    );
  }

  addDownload (download: Download): Observable<any> {
    return this.http.post<Download>(`${this.apiURL}`, download, { headers: this.options }).pipe(
      tap((download: Download) => this.log(`added download w/ key=${download._key}`)),
      catchError(this.handleError<Download>('addDownload'))
    );
  }

  updateDownload (keyDownload: string, download: any): Observable<any> {
    return this.http.put(`${this.apiURL}/${keyDownload}`, download, { headers: this.options }).pipe(
      tap((response: any) => this.log(`download updated w/ id=${response._key}`)),
      catchError(this.handleError<Download>('updateDownload'))
    )
  }

  removeDownload (keyDownload: string): Observable<any> {
    return this.http.delete(`${this.apiURL}/${keyDownload}`, { headers: this.options }).pipe(
      tap(_ => this.log(`download deleted with id=${keyDownload}.`)),
      catchError(this.handleError<Download>('removeDownload'))
    );
  }

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      this.logError(error, operation);
      return of(result as T);
    };
  }

}