import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { of } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';

import { LoggerService } from './logger.service';
import { AuthService } from './auth.service';
import { environment } from '../../../../environments/environment';
import { User } from '../classes/user';

@Injectable({
  providedIn: 'root'
})
export class UserService {

  private apiURL: string = `${environment.apiURL}/users`;
  options: HttpHeaders = new HttpHeaders();
  session: any = {};

  constructor(private http: HttpClient, private loggerService: LoggerService, private authService: AuthService) {
    this.options = new HttpHeaders( {'Content-Type': 'application/json', 'x-access-token': localStorage.getItem('tokenUserLogged')} );
  }

  private logError (error, operation) {
    let message = '';
    let reload = false;
    if (error.status === 404) {
      message = 'Falha ao se comunicar com a API: Rota no encontrada (ERRO 404)';
    } else if (error.status === 401) {
      message = 'Falha ao se comunicar com a API: Token invlido ou expirado';
      reload = this.authService.logoff();
    } else if (error.status === 400) {
      message = `Os dados enviados so invlidos! \nCausa do erro: ${error.error.details[0].message}`;
    } else if (error.status >= 500 && error.status <= 505) {
      message = `Houve algum erro no servidor! Entre em contato com o administrador!`;
    } else {
      message = error.Message;
    }

    this.loggerService.add(`UserService failed on operation: ${operation}\nMessage: ${message}`);
    if (reload) {
      location.reload();
    }
  }

  private log (mensagem: string) {
    this.loggerService.add('UserService: ' + mensagem);
  }

  getUser (keyUser: string): Observable<any> {
    return this.http.get(`${this.apiURL}/${keyUser}`, { headers: this.options }).pipe(
      tap(_ => this.log(`fetched user with id=${keyUser}`)),
      catchError(this.handleError<User>('getUser'))
    );
  }

  getUsers (): Observable<any[]> {
    return this.http.get<any[]>(`${this.apiURL}`, { headers: this.options }).pipe(
      tap(users => this.log(`fetched users`)),
      catchError(this.handleError('getUsers', []))
    );
  }

  addUser (user: User): Observable<any> {
    return this.http.post<User>(`${this.apiURL}`, user, { headers: this.options }).pipe(
      tap((user: User) => this.log(`added user w/ key=${user._key}`)),
      catchError(this.handleError<User>('addUser'))
    );
  }

  updateUser (keyUser: string, user: any): Observable<User> {
    return this.http.put(`${this.apiURL}/${keyUser}`, user, { headers: this.options }).pipe(
      tap((response: any) => this.log(`user updated w/ id=${response._key}`)),
      catchError(this.handleError<User>('updateUser'))
    )
  }

  removeUser (keyUser: string): Observable<any> {
    return this.http.delete(`${this.apiURL}/${keyUser}`, { headers: this.options }).pipe(
      tap(_ => this.log(`user deleted with id=${keyUser}.`)),
      catchError(this.handleError<User>('removeUser'))
    );
  }

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      this.logError(error, operation);
      return of(result as T);
    };
  }

}
