import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { of } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';

import { LoggerService } from './logger.service';
import { AuthService } from './auth.service';
import { environment } from '../../../../environments/environment';
import { Team } from '../classes/team';

@Injectable({
  providedIn: 'root'
})
export class TeamService {

  private apiURL: string = `${environment.apiURL}/teams`;
  options: HttpHeaders = new HttpHeaders();
  session: any = {};

  constructor(private http: HttpClient, private loggerService: LoggerService, private authService: AuthService) {
    this.options = new HttpHeaders( {'Content-Type': 'application/json', 'x-access-token': localStorage.getItem('tokenUserLogged')} );
  }

  private logError (error, operation) {
    let message = '';
    let reload = false;
    if (error.status === 404) {
      message = 'Falha ao se comunicar com a API: Rota no encontrada (ERRO 404)';
    } else if (error.status === 401) {
      message = 'Falha ao se comunicar com a API: Token invlido ou expirado';
      reload = this.authService.logoff();
    } else if (error.status === 400) {
      message = `Os dados enviados so invlidos! \nCausa do erro: ${error.error.details[0].message}`;
    } else if (error.status >= 500 && error.status <= 505) {
      message = `Houve algum erro no servidor! Entre em contato com o administrador!`;
    } else {
      message = error.Message;
    }

    this.loggerService.add(`TeamService failed on operation: ${operation}\nMessage: ${message}`);
    if (reload) {
      location.reload();
    }
  }

  private log (mensagem: string) {
    this.loggerService.add('TeamService: ' + mensagem);
  }

  getTeam (keyTeam: string): Observable<any> {
    return this.http.get(`${this.apiURL}/${keyTeam}`, { headers: this.options }).pipe(
      tap(_ => this.log(`fetched team with id=${keyTeam}`)),
      catchError(this.handleError<Team>('getTeam'))
    );
  }

  getTeams (): Observable<any[]> {
    return this.http.get<any[]>(`${this.apiURL}`, { headers: this.options }).pipe(
      tap(teams => this.log(`fetched teams`)),
      catchError(this.handleError('getTeams', []))
    );
  }

  addTeam (team: Team): Observable<any> {
    return this.http.post<Team>(`${this.apiURL}`, team, { headers: this.options }).pipe(
      tap((team: Team) => this.log(`added team w/ key=${team._key}`)),
      catchError(this.handleError<Team>('addTeam'))
    );
  }

  updateTeam (keyTeam: string, team: any): Observable<any> {
    return this.http.put(`${this.apiURL}/${keyTeam}`, team, { headers: this.options }).pipe(
      tap((response: any) => this.log(`team updated w/ id=${response._key}`)),
      catchError(this.handleError<Team>('updateTeam'))
    )
  }

  removeTeam (keyTeam: string): Observable<any> {
    return this.http.delete(`${this.apiURL}/${keyTeam}`, { headers: this.options }).pipe(
      tap(_ => this.log(`team deleted with id=${keyTeam}.`)),
      catchError(this.handleError<Team>('removeTeam'))
    );
  }

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      this.logError(error, operation);
      return of(result as T);
    };
  }

}