import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { of } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';

import { LoggerService } from './logger.service';
import { AuthService } from './auth.service';
import { environment } from '../../../../environments/environment';
import { Professor } from '../classes/professor';

@Injectable({
  providedIn: 'root'
})
export class ProfessorService {

  private apiURL: string = `${environment.apiURL}/professors`;
  options: HttpHeaders = new HttpHeaders();
  session: any = {};

  constructor(private http: HttpClient, private loggerService: LoggerService, private authService: AuthService) {
    this.options = new HttpHeaders( {'Content-Type': 'application/json', 'x-access-token': localStorage.getItem('tokenUserLogged')} );
  }

  private logError (error, operation) {
    let message = '';
    let reload = false;
    if (error.status === 404) {
      message = 'Falha ao se comunicar com a API: Rota no encontrada (ERRO 404)';
    } else if (error.status === 401) {
      message = 'Falha ao se comunicar com a API: Token invlido ou expirado';
      reload = this.authService.logoff();
    } else if (error.status === 400) {
      message = `Os dados enviados so invlidos! \nCausa do erro: ${error.error.details[0].message}`;
    } else if (error.status >= 500 && error.status <= 505) {
      message = `Houve algum erro no servidor! Entre em contato com o administrador!`;
    } else {
      message = error.Message;
    }

    this.loggerService.add(`ProfessorService failed on operation: ${operation}\nMessage: ${message}`);
    if (reload) {
      location.reload();
    }
  }

  private log (mensagem: string) {
    this.loggerService.add('ProfessorService: ' + mensagem);
  }

  getProfessor (keyProfessor: string): Observable<any> {
    return this.http.get(`${this.apiURL}/${keyProfessor}`, { headers: this.options }).pipe(
      tap(_ => this.log(`fetched professor with id=${keyProfessor}`)),
      catchError(this.handleError<Professor>('getProfessor'))
    );
  }

  getProfessors (): Observable<any[]> {
    return this.http.get<any[]>(`${this.apiURL}`, { headers: this.options }).pipe(
      tap(professors => this.log(`fetched professor categories`)),
      catchError(this.handleError('getProfessors', []))
    );
  }

  addProfessor (professor: Professor): Observable<any> {
    return this.http.post<Professor>(`${this.apiURL}`, professor, { headers: this.options }).pipe(
      tap((professor: Professor) => this.log(`added professor w/ key=${professor._key}`)),
      catchError(this.handleError<Professor >('addProfessor'))
    );
  }

  updateProfessor (keyProfessor: string, professor: any): Observable<any> {
    return this.http.put(`${this.apiURL}/${keyProfessor}`, professor, { headers: this.options }).pipe(
      tap((response: any) => this.log(`professor updated w/ id=${response._key}`)),
      catchError(this.handleError<Professor>('updateProfessor'))
    )
  }

  removeProfessor (keyProfessor: string): Observable<any> {
    return this.http.delete(`${this.apiURL}/${keyProfessor}`, { headers: this.options }).pipe(
      tap(_ => this.log(`professor deleted with id=${keyProfessor}.`)),
      catchError(this.handleError<Professor>('removeProfessor'))
    );
  }

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      this.logError(error, operation);
      return of(result as T);
    };
  }

}