import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { of } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';

import { LoggerService } from './logger.service';
import { AuthService } from './auth.service';
import { environment } from '../../../../environments/environment';
import { Approval } from '../classes/approval';

@Injectable({
  providedIn: 'root'
})
export class ApprovalService {

  private apiURL: string = `${environment.apiURL}/approvals`;
  options: HttpHeaders = new HttpHeaders();
  session: any = {};

  constructor(private http: HttpClient, private loggerService: LoggerService, private authService: AuthService) {
    this.options = new HttpHeaders( {'Content-Type': 'application/json', 'x-access-token': localStorage.getItem('tokenUserLogged')} );
  }

  private logError (error, operation) {
    let message = '';
    let reload = false;
    if (error.status === 404) {
      message = 'Falha ao se comunicar com a API: Rota no encontrada (ERRO 404)';
    } else if (error.status === 401) {
      message = 'Falha ao se comunicar com a API: Token invlido ou expirado';
      reload = this.authService.logoff();
    } else if (error.status === 400) {
      message = `Os dados enviados so invlidos! \nCausa do erro: ${error.error.details[0].message}`;
    } else if (error.status >= 500 && error.status <= 505) {
      message = `Houve algum erro no servidor! Entre em contato com o administrador!`;
    } else {
      message = error.Message;
    }

    this.loggerService.add(`ApprovalService failed on operation: ${operation}\nMessage: ${message}`);
    if (reload) {
      location.reload();
    }
  }

  private log (mensagem: string) {
    this.loggerService.add('ApprovalService: ' + mensagem);
  }

  getApproval (keyApproval: string): Observable<any> {
    return this.http.get(`${this.apiURL}/${keyApproval}`, { headers: this.options }).pipe(
      tap(_ => this.log(`fetched approval with id=${keyApproval}`)),
      catchError(this.handleError<Approval>('getApproval'))
    );
  }

  getApprovals (): Observable<any[]> {
    return this.http.get<any[]>(`${this.apiURL}`, { headers: this.options }).pipe(
      tap(approvals => this.log(`fetched approvals`)),
      catchError(this.handleError('getApprovals', []))
    );
  }

  addApproval (approval: Approval): Observable<any> {
    return this.http.post<Approval>(`${this.apiURL}`, approval, { headers: this.options }).pipe(
      tap((approval: Approval) => this.log(`added approval w/ key=${approval._key}`)),
      catchError(this.handleError<Approval>('addApproval'))
    );
  }

  updateApproval (keyApproval: string, approval: any): Observable<any> {
    return this.http.put(`${this.apiURL}/${keyApproval}`, approval, { headers: this.options }).pipe(
      tap((response: any) => this.log(`approval updated w/ id=${response._key}`)),
      catchError(this.handleError<Approval>('updateApproval'))
    )
  }

  removeApproval (keyApproval: string): Observable<any> {
    return this.http.delete(`${this.apiURL}/${keyApproval}`, { headers: this.options }).pipe(
      tap(_ => this.log(`approval deleted with id=${keyApproval}.`)),
      catchError(this.handleError<Approval>('removeApproval'))
    );
  }

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      this.logError(error, operation);
      return of(result as T);
    };
  }

}