import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { of } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';

import { LoggerService } from './logger.service';
import { AuthService } from './auth.service';
import { environment } from '../../../../environments/environment';
import { CourseCategory } from '../classes/course-category';

@Injectable({
  providedIn: 'root'
})
export class CourseCategoryService {

  private apiURL: string = `${environment.apiURL}/courses-category`;
  options: HttpHeaders = new HttpHeaders();
  session: any = {};

  constructor(private http: HttpClient, private loggerService: LoggerService, private authService: AuthService) {
    this.options = new HttpHeaders( {'Content-Type': 'application/json', 'x-access-token': localStorage.getItem('tokenUserLogged')} );
  }

  private logError (error, operation) {
    let message = '';
    let reload = false;
    if (error.status === 404) {
      message = 'Falha ao se comunicar com a API: Rota no encontrada (ERRO 404)';
    } else if (error.status === 401) {
      message = 'Falha ao se comunicar com a API: Token invlido ou expirado';
      reload = this.authService.logoff();
    } else if (error.status === 400) {
      message = `Os dados enviados so invlidos! \nCausa do erro: ${error.error.details[0].message}`;
    } else if (error.status >= 500 && error.status <= 505) {
      message = `Houve algum erro no servidor! Entre em contato com o administrador!`;
    } else {
      message = error.Message;
    }

    this.loggerService.add(`CourseCategoryService failed on operation: ${operation}\nMessage: ${message}`);
    if (reload) {
      location.reload();
    }
  }

  private log (mensagem: string) {
    this.loggerService.add('CourseCategoryService: ' + mensagem);
  }

  getCategory (keyCategory: string): Observable<any> {
    return this.http.get(`${this.apiURL}/${keyCategory}`, { headers: this.options }).pipe(
      tap(_ => this.log(`fetched course category with id=${keyCategory}`)),
      catchError(this.handleError<CourseCategory>('getCategory'))
    );
  }

  getCategories (): Observable<any[]> {
    return this.http.get<any[]>(`${this.apiURL}`, { headers: this.options }).pipe(
      tap(courseCategories => this.log(`fetched course categories`)),
      catchError(this.handleError('getCategories', []))
    );
  }

  addCategory (category: CourseCategory): Observable<any> {
    return this.http.post<CourseCategory>(`${this.apiURL}`, category, { headers: this.options }).pipe(
      tap((category: CourseCategory) => this.log(`added course category w/ key=${category._key}`)),
      catchError(this.handleError<CourseCategory >('addCategory'))
    );
  }

  updateCategory (keyCategory: string, category: any): Observable<any> {
    return this.http.put(`${this.apiURL}/${keyCategory}`, category, { headers: this.options }).pipe(
      tap((response: any) => this.log(`course category updated w/ id=${response._key}`)),
      catchError(this.handleError<CourseCategory>('updateCategory'))
    )
  }

  removeCategory (keyCategory: string): Observable<any> {
    return this.http.delete(`${this.apiURL}/${keyCategory}`, { headers: this.options }).pipe(
      tap(_ => this.log(`course category deleted with id=${keyCategory}.`)),
      catchError(this.handleError<CourseCategory>('removeCategory'))
    );
  }

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      this.logError(error, operation);
      return of(result as T);
    };
  }

}