import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { of } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';

import { LoggerService } from './logger.service';
import { AuthService } from './auth.service';
import { environment } from '../../../../environments/environment';
import { Event } from '../classes/event';

@Injectable({
  providedIn: 'root'
})
export class EventService {

  private apiURL: string = `${environment.apiURL}/events`;
  options: HttpHeaders = new HttpHeaders();
  session: any = {};

  constructor(private http: HttpClient, private loggerService: LoggerService, private authService: AuthService) {
    this.options = new HttpHeaders( {'Content-Type': 'application/json', 'x-access-token': localStorage.getItem('tokenUserLogged')} );
  }

  private logError (error, operation) {
    let message = '';
    let reload = false;
    if (error.status === 404) {
      message = 'Falha ao se comunicar com a API: Rota no encontrada (ERRO 404)';
    } else if (error.status === 401) {
      message = 'Falha ao se comunicar com a API: Token invlido ou expirado';
      reload = this.authService.logoff();
    } else if (error.status === 400) {
      message = `Os dados enviados so invlidos! \nCausa do erro: ${error.error.details[0].message}`;
    } else if (error.status >= 500 && error.status <= 505) {
      message = `Houve algum erro no servidor! Entre em contato com o administrador!`;
    } else {
      message = error.Message;
    }

    this.loggerService.add(`EventService failed on operation: ${operation}\nMessage: ${message}`);
    if (reload) {
      location.reload();
    }
  }

  private log (mensagem: string) {
    this.loggerService.add('EventService: ' + mensagem);
  }

  getEvent (keyEvent: string): Observable<any> {
    return this.http.get(`${this.apiURL}/${keyEvent}`, { headers: this.options }).pipe(
      tap(_ => this.log(`fetched event with id=${keyEvent}`)),
      catchError(this.handleError<Event>('getEvent'))
    );
  }

  getEvents (): Observable<any[]> {
    return this.http.get<any[]>(`${this.apiURL}`, { headers: this.options }).pipe(
      tap(events => this.log(`fetched new events`)),
      catchError(this.handleError('getEvents', []))
    );
  }

  addEvent (event: Event): Observable<any> {
    return this.http.post<Event>(`${this.apiURL}`, event, { headers: this.options }).pipe(
      tap((event: Event) => this.log(`added event w/ key=${event._key}`)),
      catchError(this.handleError<Event >('addEvent'))
    );
  }

  updateEvent (keyEvent: string, event: any): Observable<any> {
    return this.http.put(`${this.apiURL}/${keyEvent}`, event, { headers: this.options }).pipe(
      tap((response: any) => this.log(`event updated w/ id=${response._key}`)),
      catchError(this.handleError<Event>('updateEvent'))
    )
  }

  removeEvent (keyEvent: string): Observable<any> {
    return this.http.delete(`${this.apiURL}/${keyEvent}`, { headers: this.options }).pipe(
      tap(_ => this.log(`event deleted with id=${keyEvent}.`)),
      catchError(this.handleError<Event>('removeEvent'))
    );
  }

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      this.logError(error, operation);
      return of(result as T);
    };
  }

}