import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { of } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';

import { LoggerService } from './logger.service';
import { AuthService } from './auth.service';
import { environment } from '../../../../environments/environment';
import { New } from '../classes/new';

@Injectable({
  providedIn: 'root'
})
export class NewService {

  private apiURL: string = `${environment.apiURL}/news`;
  options: HttpHeaders = new HttpHeaders();
  session: any = {};

  constructor(private http: HttpClient, private loggerService: LoggerService, private authService: AuthService) {
    this.options = new HttpHeaders( {'Content-Type': 'application/json', 'x-access-token': localStorage.getItem('tokenUserLogged')} );
  }

  private logError (error, operation) {
    let message = '';
    let reload = false;
    if (error.status === 404) {
      message = 'Falha ao se comunicar com a API: Rota no encontrada (ERRO 404)';
    } else if (error.status === 401) {
      message = 'Falha ao se comunicar com a API: Token invlido ou expirado';
      reload = this.authService.logoff();
    } else if (error.status === 400) {
      message = `Os dados enviados so invlidos! \nCausa do erro: ${error.error.details[0].message}`;
    } else if (error.status >= 500 && error.status <= 505) {
      message = `Houve algum erro no servidor! Entre em contato com o administrador!`;
    } else {
      message = error.Message;
    }

    this.loggerService.add(`NewService failed on operation: ${operation}\nMessage: ${message}`);
    if (reload) {
      location.reload();
    }
  }

  private log (mensagem: string) {
    this.loggerService.add('NewService: ' + mensagem);
  }

  getNew (keyNew: string): Observable<any> {
    return this.http.get(`${this.apiURL}/${keyNew}`, { headers: this.options }).pipe(
      tap(_ => this.log(`fetched news with id=${keyNew}`)),
      catchError(this.handleError<New>('getNew'))
    );
  }

  getNews (): Observable<any[]> {
    return this.http.get<any[]>(`${this.apiURL}`, { headers: this.options }).pipe(
      tap(news => this.log(`fetched news`)),
      catchError(this.handleError('getNews', []))
    );
  }

  addNew (news: New): Observable<any> {
    return this.http.post<New>(`${this.apiURL}`, news, { headers: this.options }).pipe(
      tap((news: New) => this.log(`added news w/ key=${news._key}`)),
      catchError(this.handleError<New>('addNew'))
    );
  }

  updateNew (keyNew: string, news: any): Observable<any> {
    return this.http.put(`${this.apiURL}/${keyNew}`, news, { headers: this.options }).pipe(
      tap((response: any) => this.log(`news updated w/ id=${response._key}`)),
      catchError(this.handleError<New>('updateNew'))
    )
  }

  removeNew (keyNew: string): Observable<any> {
    return this.http.delete(`${this.apiURL}/${keyNew}`, { headers: this.options }).pipe(
      tap(_ => this.log(`news deleted with id=${keyNew}.`)),
      catchError(this.handleError<New>('removeNew'))
    );
  }

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      this.logError(error, operation);
      return of(result as T);
    };
  }

}